import React from 'react'
import { Tilt } from 'react-tilt'
import SectionWrapper from '../hoc/SectionWrapper'
import { styles } from '../style'
import { motion } from 'framer-motion' 
import {fadeIn,textVariant} from '../utils/motion'
import { projects } from '../constants'
import { github } from '../assets'
const Work = () => {
  return (
   <>
   <motion.div variants={textVariant()}>
      <p className={styles.sectionSubText}>What i have developed</p>
      <h2 className={styles.sectionHeadText}>Projects</h2>
    </motion.div>
    <div className='mt-5 flex flex-wrap justify-center gap-10'>
    {projects.map((pr,i)=>{
        return <motion.div variants={fadeIn("up","spring",0.5*i,0.75)}>
          <Tilt className="sm:w-[360px] w-full bg-tertiary p-5 rounded-2xl">
          <div className='relative w-full h-230px'>
            <img src={pr.image} alt={pr.name} className='w-full h-full object-cover rounded-2xl' />
            <div className='absolute inset-0 flex justify-end m-3 card-img_hover'>
              <div className='h-9 w-9 black-gradient rounded-full flex justify-center items-center cursor-pointer' onClick={()=>window.open(pr.source_code_link,"_blank")}>
                <img src={github} alt={github} />
              </div>
            </div>
          </div>
          <div className='mt-5'>
            <p className='font-bold text-white text-[24px]'>{pr.name}</p>
            <p className='mt-2 text-white text-[14px]'>{pr.description}</p>
            <ul className='mt-2 list-none flex flex-start justify-between'>
              {pr.tags.map((tg,ii)=>{
                return <li className={tg.color}>#{tg.name}</li>
              })}
            </ul>
          </div>
          </Tilt>
        </motion.div>
      })}
    </div>
   </>
  )
}

export default SectionWrapper(Work,"work")