import React from 'react'
import SectionWrapper from '../hoc/SectionWrapper'
import { styles } from '../style'
import { motion } from 'framer-motion' 
import {fadeIn,textVariant} from '../utils/motion'
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import {experiences} from '../constants'

const Experience = () => {
  return (
   <>
    <motion.div variants={textVariant()}>
      <p className={styles.sectionSubText}>What i have done so far</p>
      <h2 className={styles.sectionHeadText}>Work Experience</h2>
    </motion.div>
    <VerticalTimeline>
      {experiences.map((ex,index)=>{
        return <VerticalTimelineElement
        key={index}
        className="vertical-timeline-element--work"
        contentStyle={{ background: '#1d1836', color: '#fff' }}
        contentArrowStyle={{ borderRight: '7px solid  #232631' }}
        date={ex.date}
        iconStyle={{ background: ex.iconBg }}
        icon={
          <div className='flex justify-center items-center w-full h-full'>
            <img src={ex.icon} className='w-[60%] h-[60%] object-contain'/>
          </div>
        }
      >
        <h3 className="vertical-timeline-element-title text-white text-[24px] font-bold">{ex.title}</h3>
        <h4 className="vertical-timeline-element-subtitle text-secondary font-semibold text-[16px]">{ex.company_name}</h4>
       <ul className='list-disc mt-5 ml-5 space-y-2'>
        {ex.points.map((pt,i)=>{
          return <li className='text-white-100 text-[14px] pl-1 tracking-wider'>{pt}</li>
        })}
       </ul>
      </VerticalTimelineElement>
      })}
  
  
</VerticalTimeline>
   </>
  )
}

export default SectionWrapper(Experience,"experience")