import React, { useRef,useState } from 'react'
import { SectionWrapper } from '../hoc'
import { styles } from '../style'
import { motion } from 'framer-motion' 
import {slideIn,textVariant} from '../utils/motion'

const Contact = () => {
  const formRef = useRef();
  const [form, setForm] = useState({
    name: "",
    email: "",
    message: "",
  });

  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    const { target } = e;
    const { name, value } = target;

    setForm({
      ...form,
      [name]: value,
    });
  };

  return (
    <div className='flex gap-10 flex-row overflow-hidden'>
    <motion.div variants={slideIn("left","tween",0.2,1)}
    className='flex-[0.55] bg-black-100 p-8 rounded-2xl'>
      <p className={styles.sectionSubText}>Get in Touch</p>
      <h2 className={styles.sectionHeadText}>Contact</h2>
      <form className='mt-12 flex flex-col gap-8'>
    <label className='flex flex-col'>
      <span className='text-white font-medium mb-4'>Your Name</span>
      <input type='text' placeholder='Whats your name?' name="name" className='bg-tertiary px-6 py-4 rounded-lg outlined-none border-none font-medium placeholder:text-secondary'/>
    </label>
    <label className='flex flex-col'>
      <span className='text-white font-medium mb-4'>Email</span>
      <input type='text' placeholder='Whats your email?' name='mail' className='bg-tertiary px-6 py-4 rounded-lg outlined-none border-none font-medium placeholder:text-secondary'/>
    </label>
    <label className='flex flex-col'>
      <span className='text-white font-medium mb-4'>Message</span>
      <textarea type='text' rows={5} placeholder='Whats your message?' name='message' className='bg-tertiary px-6 py-4 rounded-lg outlined-none border-none font-medium placeholder:text-secondary'/>
    </label>
    <button type='submit' className='bg-tertiary text-white-100 px-6 py-4 rounded-lg outlined-none border-none font-medium'>
    {loading?'Sending':'Send'}
    </button>
    </form>
    </motion.div>
    <motion.div
        variants={slideIn("right", "tween", 0.2, 1)}
        className='xl:flex-1 xl:h-auto md:h-[550px] h-[350px]'
      >
        {/* <EarthCanvas /> */}
      </motion.div>
    </div>
  )
}

export default SectionWrapper(Contact,"contact")