import React from 'react'
import SectionWrapper from '../hoc/SectionWrapper'
import { styles } from '../style'
import { motion } from 'framer-motion' 
import {fadeIn,textVariant} from '../utils/motion'
import { technologies } from '../constants'
const Tech = () => {
  return (
    <>
    <motion.div variants={textVariant()}>
      <p className={styles.sectionSubText}>What i have used for development</p>
      <h2 className={styles.sectionHeadText}>Technologies</h2>
    </motion.div>
    <div className='flex flex-wrap mt-5 justify-center gap-10'>
      {technologies.map((te,i)=>{
        return <div key ={i} className='bg-secondary w-28 h-28 rounded-[50%] flex justify-center items-center'>
          <img src={te.icon} alt={te.name} className='w-[-80%] h-[80%] object-contain' />
        </div>
      })}
    </div>
    </>
  )
}

export default SectionWrapper(Tech,"tech")